import AjaxRequest from './TXMajax.js';

let uuid = () => {
  return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function(c) {
    var r = (Math.random() * 16) | 0,
      v = c == 'x' ? r : (r & 0x3) | 0x8;
    return v.toString(16);
  });
};
let getUUID = () => {
  if (window.localStorage) {
    if (localStorage.getItem('TouchQuizUUID')) {
      return localStorage.getItem('TouchQuizUUID');
    } else {
      let userUUID = uuid();
      localStorage.setItem('TouchQuizUUID', userUUID);
      return userUUID;
    }
  } else {
    return uuid();
  }
};
export const TPC = {
  tpaEvent: {
    PAGEVIEW: 'pageview',
    CLICK: 'click',
    SUB_PAGEVIEW: 'sub_pageview',
    TIME: 'time',
    OPEN: 'open',
    PRODUCT_LOADED: 'product_loaded',
  },
  tpaFrom: {
    SHARE: 'share',
    HOME: 'home',
    QUICK_PANEL: 'quickpanel',
    TOOLBAR_ICON: 'toolbaricon',
    TOOLBAR_MIDDLE: 'toolbarmiddle',
    LUCKY_DRAW: 'luckydraw',
  },
  tpaDesc: {
    DOWNLOAD: 'download_clicked',
    UPDATE: 'get_touchpal',
    MORE: 'more',
    SHARE: 'share_to_fb',
    GET_THEME: 'get', //theme自带keyboard
    GET_EMOJI: 'get_emoji',
    FILTER_AD: 'filter_ad',
  },
  tpaTarget: {},
};
export let tpa = {
  uid: undefined,
  linkFrom: undefined,
  source: undefined,
  page: undefined,
  init(page, linkFrom, source, te) {
    this.page = page;
    this.uid = getUUID();
    this.linkFrom = linkFrom;
    this.source = source;
    this.te = te;
  },
  send(event, desc, ...args) {
    let url = 'http://usa.ime.cootek.com/statistic/game';
    let d = new Date();
    let month = d.getMonth() < 10 ? '0' + (d.getMonth() + 1).toString() : (d.getMonth() + 1).toString();
    let day = d.getDate() < 10 ? '0' + d.getDate().toString() : d.getDate().toString();
    let hour = d.getHours() < 10 ? '0' + d.getHours().toString() : d.getHours().toString();
    let minute = d.getMinutes() < 10 ? '0' + d.getMinutes().toString() : d.getMinutes().toString();
    let stamp = d.getFullYear().toString() + month + day + hour + minute;

    let target;
    let payload;
    if (args.length === 0) {
      target = '';
      payload = '';
    } else if (args.length === 1) {
      target = args[0];
      payload = '';
    } else {
      target = args[0];
      payload = args[1];
    }
    let te = args[2] || '';
    new AjaxRequest().send({
      type: 'post',
      url: url,
      timeout: 100 * 1000,
      dataType: 'json',
      postData: JSON.stringify({
        page: this.page,
        uid: this.uid,
        event: event,
        timestamp: stamp,
        from: this.linkFrom,
        payload: payload,
        te: this.te,
        attribute: { source: this.source, desc: desc, target: target },
      }),
      success: res => {
        if (res.error_code == 1000) {
          console.log('post data failed');
        } else {
          console.log('post data succeed');
        }
      },
      error: () => {},
    });
  },
};



