export default function (handler, method, compatibleMethod, ...methodArguments) {
    if (typeof window[handler] !== 'undefined' && typeof window[handler][method] !== 'undefined') {
        return window[handler][method](...methodArguments);
    } else if (typeof compatibleMethod === 'function') {
        return compatibleMethod(...methodArguments)||1;
    } else {
        return false;
    }
}


